import * as React from 'react';
import { Link } from 'gatsby';

const ProjectLayout = ({ pageTitle, nextProject, previousProject, children, tags }) => {
  return (
    <main>
      <div className="container mt-5">
        <div className="grid mb-5">
          <div className="g-col-10 g-start-2">
            <nav aria-label="breadcrumb">
              <ol class="breadcrumb">
                <li class="breadcrumb-item"><Link to="/">Home</Link></li>
                <li class="breadcrumb-item active" aria-current="page">{pageTitle}</li>
              </ol>
            </nav>
            <h1>{pageTitle}</h1>
            {tags && tags.length > 1 &&
              <ul className="list-inline">
                {tags.map((tag) =>
                  <li className="list-inline-item"><span className="badge text-bg-light">{tag}</span></li>
                )}
              </ul>
            }
            {tags && tags.length === 1 &&
              <>
                {tags.map((tag) =>
                  <span className="badge text-bg-light">{tag}</span>
                )}
              </>
            }
            {children}
          </div>
        </div>
        <div className="grid">
          <div className="g-col-5 g-start-2">
            <div class="d-grid">
              <Link className="btn btn-outline-primary btn-lg" to={previousProject}>Previous Project</Link>
            </div>
          </div>
          <div className="g-col-5">
            <div class="d-grid">
              <Link className="btn btn-primary btn-lg" to={nextProject}>Next Project</Link>
            </div>
          </div>
        </div>
      </div>
    </main>
  )
}

export default ProjectLayout;